import $ from 'jquery';
import 'what-input';

// Foundation JS relies on a global variable. In ES6, all imports are hoisted
// to the top of the file so if we used `import` to import Foundation,
// it would execute earlier than we have assigned the global variable.
// This is why we have to use CommonJS require() here since it doesn't
// have the hoisting behavior.
window.jQuery = $;
// require('foundation-sites');

// If you want to pick and choose which modules to include, comment out the above and uncomment
// the line below
import './lib/foundation-explicit-pieces';

import languages from './langData';

let translitKeyboard, translitVirtual;

$(() => {
    const $txtArea = $('#txtArea');
    const $keyboard = $('#keyboard');
    const $langSelect = $('#languages');
    $txtArea.focus();
    initLanguagesSelection($langSelect, $keyboard, $txtArea);
    initGeneral($txtArea);
    initVirtualTranslitKeyboard($keyboard, $txtArea);
    initPhysicalKeyboardTransliteration($txtArea);
    $('#switch').on('change', function() {
        const isChecked = $(this).is(':checked');
        deleteVirtualKeyboard($keyboard);
        cancelKeyboardBinding($txtArea);
        if (isChecked) {
            initVirtualTranslitKeyboard($keyboard, $txtArea);
            initPhysicalKeyboardTransliteration($txtArea);
        }
        $txtArea.focus();
    });
    $(document).foundation();
});


function initGeneral($txtArea) {
    $('#copy-all').click(() => {
        $txtArea.select();
        document.execCommand('copy');
    });
    $('#translate').click(() => {
        const copiedText = $txtArea.val();
        if (copiedText) {
            window.open(`https://www.bing.com/translator?text=${copiedText}`, '_blank');
        }
    });
    const currentYear = new Date().getFullYear();
    if (currentYear > 2020) {
        $('#copyright-year').text(' - ' + currentYear);
    }
}

function initLanguagesSelection($langSelect, $keyboard, $txtArea) {
    const options = [];
    const langCodes = Object.keys(languages);
    const preselectedLangCode = getPreferredLanguageCode(langCodes) || '';
    const selectedLangData = languages[preselectedLangCode] || {};

    $keyboard = $('#keyboard');
    selectedLangData.rtl ? $txtArea.css('direction', 'rtl') : $txtArea.css('direction', '');
    selectedLangData.rtl ? $keyboard.css('direction', 'rtl') : $keyboard.css('direction', '');
    translitKeyboard = selectedLangData.translitKeyboard || {};
    translitVirtual = selectedLangData.translitVirtual || {};
    langCodes.forEach(code => 
        options.push(`<option value=${code}${code === preselectedLangCode ? ' selected' : ''}>${languages[code].label}</option>`));
    $langSelect.append(options);
    $langSelect.on('change', function(event) {
        const selectedLangData = languages[this.value] || {};
        selectedLangData.rtl ? $txtArea.css('direction', 'rtl') : $txtArea.css('direction', '');
        selectedLangData.rtl ? $keyboard.css('direction', 'rtl') : $keyboard.css('direction', '');
        translitKeyboard = selectedLangData.translitKeyboard || {};
        translitVirtual = selectedLangData.translitVirtual || {};
        try {
            localStorage.setItem('selectedLanguage', this.value);
        } catch (err) {
            console.log('Failed to write to local storage:', this.value);
        }
        const $switch = $('#switch')
        const isChecked = $switch.is(':checked');
        if (!isChecked) {
            $switch.click();
        } else {
            deleteVirtualKeyboard($keyboard);
            cancelKeyboardBinding($txtArea);
            initVirtualTranslitKeyboard($keyboard, $txtArea);
            initPhysicalKeyboardTransliteration($txtArea);
            $txtArea.focus();   
        }
    });
}

function getPreferredLanguageCode(langCodes) {
    let userLangCode;
    // Previously selected:
    try {
        userLangCode = localStorage.getItem('selectedLanguage');
    } catch (err) {
        console.log('Failed to read from local storage.');
    }
    if (userLangCode) {
        return userLangCode;
    }
    // Try guessing from the browser:
    for (let userLang of navigator.languages) {
        userLangCode = userLang.split('-')[0];
        if (langCodes.includes(userLangCode)) {
            try {
                localStorage.setItem('selectedLanguage', userLangCode);
            } catch (err) {
                console.log('Failed to write to local storage:' + userLangCode);
            }
            return userLangCode;
        }
    }
}

function initVirtualTranslitKeyboard($keyboard, $txtArea) {
    if (!translitVirtual || !translitVirtual.length) {
        $keyboard.hide();
        return;
    }
    $keyboard.show();
    const hasCapitalLetters = Boolean(translitVirtual[0].capital);
    let $vtk = ''; //virtual transliteration keyboard
    if (hasCapitalLetters) {
        $vtk = '<span class="shift"><button id="shift" class="hollow button large">Shift</button></span>';
    }
    for (let { lower, translitLower } of translitVirtual) {
        $vtk += `<span class="letter"><button type="button" class="hollow button translit-virtual large">${lower}</button><br><kbd>${translitLower}</kbd></span>`;
    }
    $keyboard.html($vtk);
    $keyboard.addClass('callout text-center');
    
    $('#shift').click(function () { // Toggle casing  
        const allButtons = $('.translit-virtual');
        const allButtonsTranslit = $('.letter > kbd');
        let isCapital = $(this).attr('data-state') === 'capital';
        for (let i = 0; i < allButtons.length; i++) {
            allButtons.eq(i).text(isCapital ? translitVirtual[i].lower : translitVirtual[i].capital);
            allButtonsTranslit.eq(i).text(isCapital ? translitVirtual[i].translitLower : translitVirtual[i].translitCapital)
        }
        $(this).attr('data-state') === 'capital' ? $(this).attr('data-state', 'lower') : $(this).attr('data-state', 'capital');
        $txtArea.focus();
    });
    $('button.translit-virtual').click(function () {
        let selectionStart = $txtArea.prop('selectionStart');
        let selectionEnd = $txtArea.prop('selectionEnd');
        const originalText = $txtArea.val();
        const typedLetter = $(this).text();
        const replacedText = originalText.substring(0, selectionStart) + typedLetter + originalText.substring(selectionEnd, originalText.length);
        $txtArea.val(replacedText);
        $txtArea.focus();
        $txtArea.prop('selectionStart', replacedText.length);
        $txtArea.prop('selectionEnd', replacedText.length);
        const $shift = $('#shift');
        if ($shift.attr('data-state') === 'capital') {
            $shift.click();
        }
    });
}

function deleteVirtualKeyboard($keyboard) {
    $keyboard.html('');
    $keyboard.removeClass('callout');
}

function initPhysicalKeyboardTransliteration($txtArea) {
    $txtArea.on('keypress', function (event) {
        const selectionStart = $txtArea.prop('selectionStart');
        const selectionEnd = $txtArea.prop('selectionEnd');
        const { replacedText, selectionStart: newSelectionStart, selectionEnd: newSelectionEnd } = 
            transliterate({ originalText: $txtArea.val(), key: event.key, selectionStart, selectionEnd });
        if (replacedText) {
            event.preventDefault();
            $txtArea.val(replacedText);
        }
        if (newSelectionStart) {
            $txtArea.prop('selectionStart', newSelectionStart);
        }
        if (newSelectionEnd) {
            $txtArea.prop('selectionEnd', newSelectionEnd);
        }
        $txtArea.scrollTop = $txtArea.scrollHeight;
    });
}

function cancelKeyboardBinding($txtArea) {
    $txtArea.off('keypress');
}

function transliterate({ originalText, key, selectionStart, selectionEnd }) {
    let replacedText;
    if (translitKeyboard[key]) {
        if (translitKeyboard[key].previous) { // 1+ to 1
            if (originalText.length > 0) {
                const previousLetter = originalText[selectionStart - 1];
                for (let pair of translitKeyboard[key].previous) {    
                    if (pair[previousLetter]) {
                        replacedText = originalText.substring(0, selectionStart - 1) + pair[previousLetter] + originalText.substring(selectionEnd, originalText.length);
                        return { replacedText, selectionStart, selectionEnd: selectionStart };
                    }
                }
            }
        }
        if (translitKeyboard[key].default) { // 1 to 1
            replacedText = originalText.substring(0, selectionStart) + translitKeyboard[key].default + originalText.substring(selectionEnd, originalText.length);
            selectionStart++;
            return { replacedText, selectionStart, selectionEnd: selectionStart };
        }
    }
    return { replacedText, selectionStart: undefined, selectionEnd: undefined };
}
